.color-solution {
    display: grid;
    grid-gap: 3.5rem;
    padding: 3rem 0;
    @media (max-width: 1366px) {
        grid-gap: 2.5rem;
        padding: 2rem 0;
    }
    @media (max-width: 767px) {
        grid-gap: 1.5rem;
        padding: 1.35rem 0;
    }
    &:not(:last-child) {
        border-bottom: 0.1rem solid $light-gray;
    }
    // .color-solution__title

    &__title {
        position: relative;
        font-weight: 600;
        cursor: pointer;
        &::after {
            content: '';
            position: absolute;
            top: 0.25rem;
            right: 0;
            width: 2.4rem;
            height: 2.4rem;
            background-image: url(../images/icons/drop.svg);
            @extend %bg-contain;
            transition: 0.3s transform;
            @media (max-width: 1366px) {
                width: 2rem;
                height: 2rem;
            }
            @media (max-width: 767px) {
                width: 1.5rem;
                height: 1.5rem;
            }
        }
        &.active {
            &:after {
                transform: rotate(180deg);
            }
        }
    }

    &__content {
        display: none;
    }
}

.colors-row {
    display: flex;
    flex-wrap: wrap;
    margin: -1.5rem -1.6rem;
    @media (max-width: 1366px) {
        margin: -1rem -1.1rem;
    }
    @media (max-width: 767px) {
        margin: -0.5rem -0.6rem;
    }
    &__item {
        width: 5rem;
        height: 5rem;
        margin: 1.6rem 1.5rem;
        cursor: pointer;
        @media (max-width: 1366px) {
            width: 3.6rem;
            height: 3.6rem;
            margin: 1rem 1.1rem;
        }
        @media (max-width: 767px) {
            width: 1.8rem;
            height: 1.8rem;
            margin: 0.5rem 0.6rem;
        }
        .radio-box:checked + .radio-style {
            .border-check__img {
                width: 34 / 50 * 100%;
                height: 34 / 50 * 100%;
                @media (max-width: 767px) {
                    width: 12 / 18 * 100%;
                    height: 12 / 18 * 100%;
                }
            }
        }
    }
}

.border-check {
    .radio-style {
        width: 100%;
        height: 100%;
        border: none;
    }
    .radio-box:checked + .radio-style {
        position: relative;
        background-color: transparent;
        &::before {
            content: '';
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background-color: #fff;
            border: 0.1rem solid $accent;
        }
    }
    // .border-check__img

    &__img {
        position: relative;
        z-index: 10;
        width: 100%;
        height: 100%;
        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
            transition: 0.3s all;
        }
    }
}
