// colors
$accent: #444;
$gray: #7a7a7a;
$light-gray: #c1c1c1;

%grid-row {
	display: grid;
	grid-auto-columns: max-content;
	grid-auto-flow: column;
}

%dewi {
	font-family: 'RF Dewi Extended', sans-serif;
	font-weight: 600;
}

// Typography
%text-12 {
	font-size: 1.2rem;
	line-height: 1.8rem;
}

%text-14 {
	font-size: 1.4rem;
	line-height: 2.1rem;
}

%text-16 {
	font-size: 1.6rem;
	line-height: 2.4rem;
}

%text-18 {
	font-size: 1.8rem;
	line-height: 2.7rem;

	@media (max-width: 1366px) {
		font-size: 1.4rem;
		line-height: 2.1rem;
	}

	@media (max-width: 1024px) {
		font-size: 1.2rem;
		line-height: 1.8rem;
	}
}

%text-20 {
	font-size: 2rem;
	line-height: 2.3rem;
}

%text-24 {
	@extend %dewi;
	font-size: 2.4rem;
	line-height: 2.9rem;

	@media (max-width: 1366px) {
		font-size: 1.8rem;
		line-height: 2.2rem;
	}

	@media (max-width: 767px) {
		font-size: 1.2rem;
		line-height: 1.6rem;
	}
}

%text-30 {
	font-size: 3rem;
	line-height: 4rem;
}

%text-32 {
	font-size: 3.2rem;
	line-height: 3.5rem;

	@media(max-width: 1366px) {
		font-size: 2.4rem;
		line-height: 2.9rem;
	}

	@media(max-width: 1024px) {
		font-size: 1.6rem;
		line-height: 1.9rem;
	}

	@media(max-width: 767px) {
		font-size: 1.4rem;
		line-height: 1.8rem;
	}
}

%text-36 {
	font-size: 3.6rem;
	line-height: 4.4rem;
}

%text-48 {
	@extend %dewi;
	font-weight: 600;
	font-size: 4.8rem;
	line-height: 5.3rem;

	@media (max-width: 1366px) {
		font-size: 3.2rem;
		line-height: 3.5rem;
	}

	@media (max-width: 767px) {
		font-size: 1.8rem;
		line-height: 2.2rem;
	}
}

%grid-center {
	display: grid;
	place-items: center;
}

// flex
%vertical-align {
	display: flex;
	align-items: center;
}

%flex-center {
	@extend %vertical-align;
	justify-content: center;
}

%flex-row {
	display: flex;
	justify-content: space-between;
}

%flex-row-center {
	@extend %flex-row;
	align-items: center;
}

%flex-col {
	display: flex;
	flex-direction: column;
}

%flex-col-center {
	@extend %flex-col;
	align-items: center;
}

// positioning
%pos-center-x {
	left: 50%;
	transform: translateX(-50%);
}

%pos-center-y {
	top: 50%;
	transform: translateY(-50%);
}

%pos-center-xy {
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
}

// background
%cover {
	width: 100%;
	height: 100%;
	object-fit: cover;
}

%bg-cover {
	background-size: cover;
	background-position: center;
	background-repeat: no-repeat;
}

%bg-contain {
	background-size: contain;
	background-position: center;
	background-repeat: no-repeat;
}
